import axios from 'axios';
import { useEffect, useState } from 'react';
export default ((url, defaultValue) => {
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState(defaultValue);
  useEffect(() => {
    setLoading(true);
    axios.get(url).then(res => {
      setData(res.data);
    }).finally(() => {
      setLoading(false);
    });
  }, [url]);
  return [loading, data];
});